.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* App.css */

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(135deg, #4e2a8e, #7a52cc);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.login-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5);
  text-align: center;
  width: 100%;
  max-width: 450px; /* Maximum width to ensure it doesn't get too wide on large screens */
  height: 100%;
  max-height: 600px; /* Maximum height for the form */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1 {
  font-size: 28px;
  margin-bottom: 20px;
}

.input-container {
  margin-bottom: 20px;
  text-align: left;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input[type="text"],
textarea,
select {
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 16px;
  margin-bottom: 10px;
  outline: none;
}

input[type="text"]::placeholder,
textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

textarea {
  resize: none;
  height: 80px;
}

button[type="submit"] {
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 5px;
  background-color: #5a4cad;
  color: white;
  font-size: 18px;
  cursor: pointer;
  margin-top: 20px;
}

button[type="submit"]:hover {
  background-color: #473b8d;
}

.error-message {
  color: #ff4d4d;
}

.success-message {
  color: #4caf50;
}
